.gpt3__blog {
    display: flex;
    flex-direction: column;
    padding: 4rem 6rem;
}

.gpt3__blog-text {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}

.gpt3__blog-text h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;

    letter-spacing: -0.04em;
}

.gpt3__blog-container {
    display: flex;
    flex-direction: row;
}

.gpt3__blog-container__groupA {
    flex: 0.75;
    margin-right: 2rem;
}

.gpt3__blog-container__groupB {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .gpt3__blog-container {
        flex-direction: column-reverse;
    }

    .gpt3__blog-container__groupA {
        margin: 2rem 0;
    }

    .gpt3__blog-container__groupA .gpt3__article{
        width: 48%;
    }

    .gpt3__blog-container__groupA .gpt3__article-image{
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__blog{
        padding: 4rem;
    }

    .gpt3__blog-container__groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .gpt3__blog-container__groupA .gpt3__article{
        width: 100%;
    }

    .gpt3__blog-text h1{
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__blog{
        padding: 2rem;
    }

    .gpt3__blog-text h1{
        font-size: 34px;
        line-height: 42px;
    }

    .gpt3__blog-container__groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .gpt3__blog-container__groupA .gpt3__article{
        width: 100%;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__blog{
        padding: 2rem;
    }

    .gpt3__blog-text h1{
        font-size: 29px;
        line-height: 35px;
    }

    .gpt3__blog-container__groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .gpt3__blog-container__groupA .gpt3__article{
        width: 100%;
    }
}